import React from "react";
import { Icon } from "antd-mobile";
require("./style.scss");

const ProtectionDeviceNumber: any = React.memo((props: any) => {
  const { children, isLoading } = props;
  return (
    <div className="protection-deviceNumber-wrap">
      {isLoading ? (
        <div className="protection-deviceNumber-loading">
          <Icon type="loading" />
        </div>
      ) : (
        ""
      )}
      <div className="protection-deviceNumber">
        <div className="protection-deviceNumber-title">防区设备（个）</div>
        <ul className="protection-deviceNumber-list">{children}</ul>
      </div>
    </div>
  );
});

const Item = React.memo((props: any) => {
  const { statusName, total = 0 } = props;
  return (
    <li className="protection-deviceNumber-item">
      {statusName}: {total}
    </li>
  );
});

ProtectionDeviceNumber.item = Item;

export default ProtectionDeviceNumber;
