import React from 'react';
// import clsx from 'clsx';
import BreadcrumbCollapsed from "./BreadcrumbCollapsed";

// 定义样式

export const styles = {
    root: {},
    ol: {
        display: 'flex',
        flexWrap: 'wrap',
        alignItem: 'center',
        padding: 0,
        margin: 0,
        listStyle: 'none',
    },
    li: {},
    separator: {
        display: 'flex',
        userSelect: 'none',
        marginLeft: 8,
        marginRight: 8,
    }
}

// 插入面包屑的分割符
const insertSeparators = (items, separator) => {
    return items.reduce((acc, current, index) => {
        if(index < items.length - 1) {
            acc = acc.concat(
                current,
                <li key={`separator-${index}`} className="breadcrumb-separator">
                    {separator}
                </li>
            )
        }else {
            acc.push(current);
        }
        return acc;
    }, []);
}

// React.forwardRef => 创建一个React组件，这个组件能够将其接受的 ref 属性转发到其组件树下的另一个组件中

const Breadcrumbs = function Breadcrumbs(props) {
    // 定义 面包屑组件接收的外部props值
    const {
        children,
        classes,
        className,
        expandText = '展开',
        itemsAfterCollapse = 1,
        itemsBeforeCollapse = 1,
        maxItems = 8,
        separator = '/',
        ...other // 扩展属性
    } = props;

    const [expanded, setExpanded] = React.useState(false);

    // 面包屑收起展开功能
    const renderItemsBeforeAndAfter = (allItems) => {
        const handleClickExpand = () => {
            setExpanded(true);
        }
        const len = allItems.length;
        if(itemsBeforeCollapse + itemsAfterCollapse >= len) {
            return allItems;
        }

        return [
            ...allItems.slice(0, itemsBeforeCollapse),
            <BreadcrumbCollapsed key='ellipsis' onClick={handleClickExpand} />,
            ...allItems.slice(len - itemsAfterCollapse, len)
        ]
    }

    const allItems = React.Children.toArray(children).filter((child) => {
        return React.isValidElement(child);
    }).map((child, index) => {
        return (
            <li className="breadcrumb-li" key={`child-${index}`}>
                {child}
            </li>
        )
    });

    return (
        <div
            // className={clsx(classes.root, className)}
        >
            <ol className="breadcrumb-ol">
                {insertSeparators(
                    expanded || maxItems && allItems.length <= maxItems ? allItems : renderItemsBeforeAndAfter(allItems), 
                    // classes.separator, 
                    separator
                )}
            </ol>

        </div>
    )

}

export default Breadcrumbs;