import React, { useEffect, useState, useRef } from "react";
import { Icon } from "antd-mobile";
import classNames from "classnames";
import { IDropdown } from "../interface";
import { getGroupData, getBuildData, getFloorData, getDeviceStatus } from "@/service/http/areasData";
import storage from "@/utils/storage";
require("./style.scss");

interface IAreaListProps {
  isLoading: boolean;
  data: IDropdown[];
  onSelect: Function;
  className?: string;
  hasProtection?: boolean;
  hasVideo?: boolean;
  pid?: any;
}

const AreaList = (props: IAreaListProps) => {
  let groupAndBuild: any = storage.getItem("groupAndBuild");
  let floorAndArea: any = storage.getItem("floorAndArea");
  groupAndBuild = groupAndBuild ? JSON.parse(groupAndBuild) : null;
  floorAndArea = floorAndArea ? JSON.parse(floorAndArea) : null;
  const { isLoading, hasProtection = false, hasVideo = false, pid, data = [], onSelect } = props;
  const [protectionData, setProtectionData] = useState({});
  const [groupAndBuildData, setGroupAndBuildData] = useState(groupAndBuild);
  const [floorAndAreaData, setfloorAndAreaData] = useState(floorAndArea);
  const updateprotectionData = (data: any[]) => {
    if (Array.isArray(data)) {
      setProtectionData(
        data
          .map(item => ({
            name: item.view_defense_area_device_total_locationName,
            id: item.view_defense_area_device_total_locationId,
            count: item.view_defense_area_device_total_id,
            useCount: item.view_defense_area_device_total_statusId
          }))
          .reduce((a: any, b: any) => {
            a[b.id] = b;
            return a;
          }, {})
      );
    }
  };

  const updateVideoData = (data: any[], buildId?: string) => {
    if (Array.isArray(data)) {
      let videoData = data.reduce((a: any, b: any) => {
        a[b.id] = b;
        return a;
      }, {});
      if (buildId) {
        setfloorAndAreaData(videoData);
        storage.setItem("floorAndArea", JSON.stringify({ buildId, data: videoData }));
      } else {
        setGroupAndBuildData(videoData);
        storage.setItem("groupAndBuild", JSON.stringify(videoData));
      }
    }
  };

  useEffect(() => {
    if (pid && pid.id && pid !== "root" && pid.type) {
      if (hasProtection) {
        switch (pid.type) {
          case "groupId":
            getGroupData({ groupId: pid.id }).then((res: any) => {
              const { result } = res;
              if (result && result.aaData) {
                updateprotectionData(result.aaData);
              }
            });
            break;
          case "buildId":
            getBuildData({ buildId: pid.id }).then((res: any) => {
              const { result } = res;
              if (result && result.aaData) {
                updateprotectionData(result.aaData);
              }
            });
            break;
          case "floorId":
            getFloorData({ floorId: pid.id }).then((res: any) => {
              const { result } = res;
              if (result && result.aaData) {
                updateprotectionData(result.aaData);
              }
            });
            break;
          default:
            break;
        }
      } else if (hasVideo) {
        switch (pid.type) {
          case "all":
            getDeviceStatus({ type: 1 }).then((res: any) => {
              const { result } = res;
              if (result) {
                updateVideoData(result);
              }
            });
            break;
          case "buildId":
            getDeviceStatus({ type: 5, nodeId: [pid.id] }).then((res: any) => {
              const { result } = res;
              if (result) {
                updateVideoData(result, pid.id);
              }
            });
            break;
          default:
            break;
        }
      }
    }
  }, [pid]);

  return (
    <div className={classNames("areaList-content", props.className)}>
      {isLoading && (
        <div className="arealist-loading">
          <Icon type="loading" />
        </div>
      )}
      {!isLoading && data.length === 0 && (
        <div className="arealist-loading">
          <span>暂无数据</span>
        </div>
      )}
      <ul className="areaList-ul">
        {data.map((d: IDropdown) => {
          return (
            <li
              key={d.id}
              data-id={d.id}
              onClick={() => {
                onSelect && onSelect(d);
              }}
            >
              <div className="areaList-item-name">{d.name}</div>

              {(() => {
                if (hasProtection && protectionData[d.id]) {
                  return (
                    <div className="areaList-item-main">
                      <span className="areaList-item-state">已布防</span>
                      <span className="areaList-item-value">
                        {protectionData[d.id].useCount || 0}/{protectionData[d.id].count || 0}
                      </span>
                      <Icon type="right" size="sm" />
                    </div>
                  );
                } else if (hasVideo && ((groupAndBuildData && groupAndBuildData[d.id]) || (floorAndAreaData && floorAndAreaData[d.id]))) {
                  let o = groupAndBuildData[d.id] || floorAndAreaData[d.id];
                  return (
                    <div className="areaList-item-main">
                      <span className="areaList-item-value">{o.total || 0}</span>
                      <Icon type="right" size="sm" />
                    </div>
                  );
                } else {
                  return (
                    <div className="areaList-item-main">
                      <Icon type="right" size="sm" />
                    </div>
                  );
                }
              })()}
            </li>
          );
        })}
      </ul>
    </div>
  );
};

export default AreaList;
