import React from "react";
import classNames from "classnames";
import { inject, observer } from "mobx-react";
import { Icon } from "antd-mobile";
import HyBreadcrumb from "@/controls/hy-breadcrumb";
import AreaList from "./List";
import { IDropdown } from "./interface";
import { toJS } from "mobx";
require("./style.scss");

const IDS_STR = ["all", "groupId", "buildId", "floorId", "areaId"];

@inject("AreaSelectStore")
@observer
class AreaSelect extends React.Component<GProps, GState> {
  componentDidMount() {
    const {
      AreaSelectStore: { fetchData }
    } = this.props;
    fetchData({ type: 12 });
  }

  componentWillUnmount() {
    const {
      stateKeep,
      AreaSelectStore: { reset }
    } = this.props;
    if (!stateKeep) {
      reset();
    }
  }

  onDreadcrumbClick = (item: IDropdown, i: number) => {
    const {
      onChange,
      AreaSelectStore: { findGroupData, fetchData, updatePid, updateBreadcrumbsLength, showDropdownChange }
    } = this.props;
    let level;
    if (item.id === "root") {
      fetchData({ type: 12 });
      updatePid();
      level = "all";
    } else if (item.extraKeyData && Number(item.extraKeyData.LocationType) === 0) {
      const hasData = findGroupData(item.id);
      if (!hasData) {
        fetchData({ type: 1, nodeId: [item.id] });
      }
      updatePid(item);
      level = "groupId";
    } else if (item.extraKeyData && Number(item.extraKeyData.LocationType) === 1) {
      fetchData({ type: 2, nodeId: [item.id] });
      updatePid(item);
      level = "buildId";
    } else if (item.extraKeyData && Number(item.extraKeyData.LocationType) === 2) {
      fetchData({ type: 3, nodeId: [item.id] });
      updatePid(item);
      level = "floorId";
    } else if (item.extraKeyData && Number(item.extraKeyData.LocationType) === 3) {
      level = "areaId";
    }
    updateBreadcrumbsLength(i + 1);
    onChange && onChange(level, item.id);
    showDropdownChange();
  };

  onDropdownSelect = (dropdown: IDropdown) => {
    const {
      onChange,
      AreaSelectStore: { findGroupData, fetchData, updatePid, updateBreadcrumbs, showDropdownChange }
    } = this.props;
    const {
      extraKeyData: { LocationType }
    } = dropdown;
    let level;
    if (Number(LocationType) === 0) {
      const hasData = findGroupData(dropdown.id);
      if (!hasData) {
        fetchData({ type: 1, nodeId: [dropdown.id] });
      }
      updatePid(dropdown);
      level = "groupId";
    } else if (Number(LocationType) === 1) {
      fetchData({ type: 2, nodeId: [dropdown.id] });
      updatePid(dropdown);
      level = "buildId";
    } else if (Number(LocationType) === 2) {
      fetchData({ type: 3, nodeId: [dropdown.id] });
      updatePid(dropdown);
      level = "floorId";
    } else if (Number(LocationType) === 3) {
      level = "areaId";
    }
    updateBreadcrumbs(dropdown);
    onChange && onChange(level, dropdown.id);
    showDropdownChange(false);
  };

  render() {
    const {
      children,
      hasProtection,
      hasVideo,
      AreaSelectStore: { isLoading, pid, showDropdown, breadcrumbs, dropdowns }
    } = this.props;

    return (
      <div className="breadcrumb-wrap">
        <HyBreadcrumb>
          {breadcrumbs &&
            breadcrumbs.length > 0 &&
            breadcrumbs.map((item: IDropdown, i: number) => (
              <div
                className={classNames({
                  // "breadcrumb-end": item.id !== "root" && (!item.extraKeyData || Number(item.extraKeyData.LocationType) === 3)
                  "breadcrumb-end": i === breadcrumbs.length - 1
                })}
                key={item.id}
                onClick={() => {
                  this.onDreadcrumbClick(item, i);
                }}
              >
                {item.name}
              </div>
            ))}
        </HyBreadcrumb>
        {showDropdown || !children ? (
          <AreaList
            pid={pid}
            className="breadcrumb-dropdown"
            isLoading={isLoading}
            data={dropdowns}
            onSelect={this.onDropdownSelect}
            hasProtection={hasProtection}
            hasVideo={hasVideo}
          />
        ) : (
          children
        )}
      </div>
    );
  }
}

export default AreaSelect;
