import { observable, action } from "mobx";
import DataApiService from "@/service/http";

class Store {
  @observable modules: any = undefined;
  @observable children: any = null;
  @observable error: any = undefined;
  @observable photo: any = undefined;

  @observable height: string = "20%";

  @observable defenceAreaSelected: string = "all";
  @observable hostSelected: string = "all";
  @observable defenceAreaData: any[] = [];
  @observable hostData: any[] = [];
  @observable deviceGroupStatus: object = {};

  @observable location: { idKey: string; id?: string | null } = { idKey: "all" };

  @action setLocation = (location: { idKey: string; id?: string | null }) => {
    this.location = location;
  };

  @action resetStore = () => {
    this.defenceAreaSelected = "all";
    this.hostSelected = "all";
    this.defenceAreaData = [];
    this.hostData = [];
    this.deviceGroupStatus = {};
  };

  @action
  fetchDeviceGroupStatus = () => {
    const { idKey, id } = this.location;
    const filterCondition: object[] = [];
    if (idKey !== "all") {
      filterCondition.push({
        type: "1",
        field: `view_defense_area_information.${idKey}`,
        condition: "=",
        connection: "&amp;&amp;",
        id: 1,
        pid: 0,
        value: id
      });
    }
    return new Promise((resolve, reject) => {
      DataApiService.getShowTableData({
        tableName: "view_defense_area_information",
        start: 0,
        type: "TABLE_QUERY",
        params: {
          filterCondition
        },
        fieldName: ["view_defense_area_information.id", "view_defense_area_information.status"],
        statisticsColumn: [
          {
            tableName: "view_defense_area_information",
            columnType: "GROUP_BY",
            columnName: "status"
          },
          {
            tableName: "view_defense_area_information",
            columnType: "COUNT",
            columnName: "id"
          }
        ]
      })
        .then(data => {
          const { result, success, message } = data;
          if (!success) {
            reject(message);
            return;
          }
          result.aaData.unshift({
            view_defense_area_information_id: result.aaData.reduce((a: number, b: any) => a + Number(b.view_defense_area_information_id), 0),
            view_defense_area_information_status: "all"
          });
          const deviceGroupStatus = {};
          result.aaData.forEach(item => {
            deviceGroupStatus[item.view_defense_area_information_status] = item.view_defense_area_information_id;
          });

          this.deviceGroupStatus = deviceGroupStatus;
          resolve(true);
        })
        .catch(err => {
          this.deviceGroupStatus = {};
          reject(err);
        });
    });
  };

  @action.bound
  fetchDefenseArea(skip, limit, location) {
    const filterCondition: object[] = [];
    if (this.defenceAreaSelected !== "all") {
      filterCondition.push({
        type: "1",
        field: "view_defense_area_information_main.status",
        condition: "=",
        connection: "&amp;&amp;",
        id: 1,
        pid: 0,
        value: this.defenceAreaSelected
      });
    }
    if (location && location.idKey !== "all" && location.id) {
      filterCondition.push({
        type: "1",
        field: `view_defense_area_information_main.${location.idKey}`,
        condition: "=",
        connection: "&amp;&amp;",
        id: 1,
        pid: 0,
        value: location.id
      });
    }
    return new Promise((resolve, reject) => {
      DataApiService.getShowTableData({
        tableName: "view_defense_area_information_main",
        length: limit,
        start: skip,
        type: "TABLE_QUERY",
        params: Object.assign(
          {},
          filterCondition.length > 0 && {
            filterCondition
          }
        ),
        fieldName: [
          "view_defense_area_information_main.id",
          "view_defense_area_information_main.name",
          "view_defense_area_information_main.status",
          "view_defense_area_information_main.mainFrameName",
          "view_defense_area_information_main.areaLocationName"
        ]
      })
        .then(data => {
          const { success } = data;
          if (success) {
            resolve(data.result);
          } else {
            reject(new Error("数据错误，请确认"));
          }
        })
        .catch(err => {
          reject(err);
        });
    });
  }

  @action.bound
  fetchDefenseHost(skip, limit, location) {
    const filterCondition: object[] = [];
    if (this.hostSelected !== "all") {
      filterCondition.push({
        type: "1",
        field: "view_defense_host_information.check_value",
        condition: "=",
        connection: "&amp;&amp;",
        id: 1,
        pid: 0,
        value: this.hostSelected
      });
    }
    if (location && location.idKey !== "all" && location.id) {
      filterCondition.push({
        type: "1",
        field: `view_defense_host_information.${location.idKey}`,
        condition: "=",
        connection: "&amp;&amp;",
        id: 1,
        pid: 0,
        value: location.id
      });
    }
    return new Promise((resolve, reject) => {
      DataApiService.getShowTableData({
        tableName: "view_defense_host_information",
        length: limit,
        start: skip,
        type: "TABLE_QUERY",
        params: Object.assign(
          {},
          filterCondition.length > 0 && {
            filterCondition
          }
        ),
        fieldName: [
          "view_defense_host_information.id",
          "view_defense_host_information.name",
          "view_defense_host_information.DeviceIp",
          "view_defense_host_information.DevicePort",
          "view_defense_host_information.areaLocationName",
          "view_defense_host_information.check_value"
        ]
      })
        .then(data => {
          const { success } = data;
          if (success) {
            resolve(data.result);
          } else {
            reject(new Error("数据错误，请确认"));
          }
        })
        .catch(err => {
          reject(err);
        });
    });
  }

  @action.bound
  defenceAreaChange(value: string) {
    this.defenceAreaSelected = value;
  }

  @action.bound
  hostChange(value: string) {
    this.hostSelected = value;
  }
}

export default new Store();
