import React from "react";

const ProtectionListItem = (props: any) => {
  const { title, mainDescription, secondaryDescription, state, handle, onClick } = props;
  return (
    <a
      className="protection-listItem"
      onClick={() => {
        onClick && onClick();
      }}
    >
      <div className="protection-listItem-main">
        <h3>{title}</h3>
        <span className="protection-listItem-main-title">{mainDescription}</span>
        <span className="protection-listItem-main-desc">{secondaryDescription}</span>
      </div>
      {state}
      <span className="protection-listItem-handle">{handle}</span>
    </a>
  );
};

export default ProtectionListItem;
