import React from 'react';
import { Icon } from 'antd-mobile';

const ProtectionListItemState = props => {
    const { state, children } = props;
    return (
        <span
            className={`protection-listItem-state protection-listItem-state_${state}`}
        >
            {children}
        </span>
    );
};

export default ProtectionListItemState;
