import React from 'react';
import { Picker, Icon } from 'antd-mobile';
require('./style.scss');

const ProtectionPicker = React.forwardRef((props: any, ref) => {
    const { data, select, onChange } = props;
    const returnLabel = () => {
        const selectItem = data.find((d) => d.value === select);
        return selectItem ? selectItem.label : '未知';
    };

    return (
        <div className="protection-picker" ref={ref}>
            <div className="protection-picker-label">状态</div>
            <Picker
                data={data}
                cols={1}
                value={[select]}
                onChange={([value]) => {
                    onChange && onChange(value);
                }}
            >
                <div className="protection-picker-select">
                    <span>{returnLabel()}</span>
                    <Icon
                        className="protection-picker-more"
                        type="down"
                        size="sm"
                    />
                </div>
            </Picker>
        </div>
    );
});

export default ProtectionPicker;
