import React from "react";
import { DEFENSE_STATUS } from "@/pages/protection/constant";
require("./style.scss");

const DefenseStatus = (props: any) => {
  const { status } = props;
  return <span className={`defense-status defense-status_${status}`}>{DEFENSE_STATUS[Number(status)] || "无"}</span>;
};

export default DefenseStatus;
