import React, { useEffect, useState, useRef } from "react";
import Item from "./Item";
import State from "./State";
import { ListView, Toast, Icon } from "antd-mobile";
require("./style.scss");

const ProtectionList = props => {
  const { fetchData, renderRow, limit, height } = props;
  const lv = useRef(null);
  const [hasMore, setHasMore] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [dataSource, setDataSource] = useState(
    new ListView.DataSource({
      rowHasChanged: (row1, row2) => row1 !== row2
    })
  );

  const currentSkipRef = useRef(0);
  const currentDataRef = useRef([]);

  const reset = () => {
    lv.current.scrollTo(0, 0);

    currentSkipRef.current = 0;
    currentDataRef.current = [];

    setHasMore(true);
    setIsLoading(false);
    setDataSource(
      new ListView.DataSource({
        rowHasChanged: (row1, row2) => row1 !== row2
      })
    );
  };

  const getData = () => {
    setIsLoading(true);
    fetchData(currentSkipRef.current, props.limit, props.location)
      .then(res => {
        let newData = [...currentDataRef.current, ...res.aaData];

        currentSkipRef.current += props.limit;
        currentDataRef.current = newData;

        setDataSource(dataSource.cloneWithRows(newData));
        setHasMore(newData.length >= res.iTotalRecords ? false : true);
      })
      .catch(err => {
        Toast.info("请求失败", 1);
      })
      .finally(() => {
        setIsLoading(false);
      });
  };

  const onEndReached = () => {
    if (!isLoading && hasMore) {
      getData();
    }
  };

  useEffect(() => {
    reset();
    setTimeout(() => {
      getData();
    }, 50);
  }, [props.select, props.location]);

  return (
    <ListView
      ref={lv}
      dataSource={dataSource}
      renderFooter={() => (
        <div className="meetinglist-tip">
          {isLoading ? <Icon type="loading" /> : currentDataRef.current && currentDataRef.current.length > 0 ? "加载完成" : "暂无数据"}
        </div>
      )}
      renderRow={renderRow}
      pageSize={limit || 20}
      style={{
        height: height
      }}
      scrollRenderAheadDistance={500}
      onEndReached={onEndReached}
      onEndReachedThreshold={10}
    />
  );
};

export { Item, State };

export default ProtectionList;
