import React, { useContext, useEffect, useRef, useState } from "react";
import { observer } from "mobx-react";
import { Tabs, Icon, Button } from "antd-mobile";
import AreaSelect from "@/components/AreaSelect2";
import List, { Item, State } from "@/components/ProtectionList";
import DeviceNumber from "./DeviceNumber";
import Picker from "./Picker";
import SubPage from "@/components/SubPage";
import DefenseStatus from "@/components/DefenseStatus";
import AreaList from "@/components/ProtectionAreaList";
import { DEFENSE_STATUS } from "../constant";
import EasyNarBar from "@/components/EasyNarBar";
const tabs = [{ title: "防区列表" }, { title: "主机列表" }];

const EMPTY_TEXT = "无";
const AREA_LIMIT = 20;
const HOST_LIMIT = 20;

const statistics_status = (() => {
  const keys = Object.keys(DEFENSE_STATUS);
  const data = keys
    .map(key => ({
      value: key,
      label: DEFENSE_STATUS[key]
    }))
    .filter(item => ["布防", "撤防", "旁路", "未知"].indexOf(item.label) !== -1);
  data.unshift({ value: "all", label: "总数" });
  return data;
})();

const defense_status = (() => {
  const keys = Object.keys(DEFENSE_STATUS);
  const data = keys
    .map(key => ({
      value: key,
      label: DEFENSE_STATUS[key]
    }))
    .filter(item => ["布防", "撤防", "旁路", "未知"].indexOf(item.label) !== -1);
  data.unshift({ value: "all", label: "全部" });
  return data;
})();

const host_status = (() => {
  const keys = Object.keys(DEFENSE_STATUS);
  const data = keys
    .map(key => ({
      value: key,
      label: DEFENSE_STATUS[key]
    }))
    .filter(item => ["在线", "离线"].indexOf(item.label) !== -1);
  data.unshift({ value: "all", label: "全部" });
  return data;
})();

const Home = observer((props: any) => {
  const { store, history } = props;
  const {
    deviceGroupStatus,
    defenceAreaSelected,
    hostSelected,
    defenceAreaChange,
    hostChange,
    fetchDefenseArea,
    fetchDefenseHost,
    resetStore,

    location,
    setLocation
  } = store;
  const lv = useRef(null);
  const pickerRef = useRef(null);
  const [height, setHeight] = useState(0);
  const [isStatisticsLoading, setIsStatisticsLoading] = useState(false);
  // const [location, setLocation] = useState({ idKey: "all", id: null });

  const returnIP = (ip: any, port: any) => {
    if (ip && port) {
      return `IP端口:${ip}:${port}`;
    }
    return `IP端口:${EMPTY_TEXT}`;
  };

  const onAreaChange = (idKey: string, id?: string) => {
    setLocation({ idKey, id });
    fetchDeviceGroupStatus();
  };

  const fetchDeviceGroupStatus = () => {
    setIsStatisticsLoading(true);
    props.store.fetchDeviceGroupStatus().then(() => {
      setIsStatisticsLoading(false);
    });
  };

  useEffect(() => {
    fetchDeviceGroupStatus();
    const navbarDom: Element | null = document.querySelector(".am-navbar");
    const breadcrumbDom: Element | null = document.querySelector(".breadcrumb-wrap");
    const tabbarDom: Element | null = document.querySelector(".am-tabs-tab-bar-wrap");
    const pickerDom: Element | null = document.querySelector(".protection-picker");
    setHeight(
      Math.max(document.documentElement.clientHeight, document.body.clientHeight) -
        (navbarDom.offsetHeight || 0) -
        (breadcrumbDom.offsetHeight || 0) -
        (tabbarDom.offsetHeight || 0) -
        (pickerDom.offsetHeight || 0)
    );
    return () => {
      // resetStore();
    };
  }, []);

  return (
    <>
      <EasyNarBar
        mainContent="布防管理"
        leftContent={<Icon type="left" />}
        onLeftClick={() => {
          history.push({ pathname: "/dashboard" });
        }}
        rightContent={
          <span
            onClick={() => {
              history.push("/follow");
            }}
            style={{ fontSize: "15px" }}
          >
            我的关注
          </span>
        }
      ></EasyNarBar>

      <div className="protection-content">
        <AreaSelect onChange={onAreaChange} hasProtection stateKeep>
          <DeviceNumber isLoading={isStatisticsLoading}>
            {statistics_status.map((item, i) => (
              <DeviceNumber.item key={item.value} statusName={item.label} total={deviceGroupStatus[item.value]} />
            ))}
          </DeviceNumber>
        </AreaSelect>
        <div ref={lv}>
          <Tabs tabs={tabs} initialPage={0} animated={false} useOnPan={false} swipeable={false}>
            <div>
              <Picker data={defense_status} select={defenceAreaSelected} onChange={defenceAreaChange} ref={pickerRef} />
              <List
                select={defenceAreaSelected}
                location={location}
                fetchData={fetchDefenseArea}
                limit={AREA_LIMIT}
                height={height}
                renderRow={(d: any) => (
                  <Item
                    key={d.view_defense_area_information_main_id}
                    title={d.view_defense_area_information_main_name || EMPTY_TEXT}
                    mainDescription={`所属主机:${d.view_defense_area_information_main_mainFrameName || EMPTY_TEXT}`}
                    secondaryDescription={`区域信息:${d.view_defense_area_information_main_areaLocationName || EMPTY_TEXT}`}
                    state={
                      <State>
                        <DefenseStatus status={d.view_defense_area_information_main_status} />
                      </State>
                    }
                    handle={<Icon type="right" size="sm" />}
                    onClick={() => {
                      history.push(`/protection/detail/${d.view_defense_area_information_main_id}`);
                    }}
                  />
                )}
              />
            </div>
            <div>
              <Picker data={host_status} select={hostSelected} onChange={hostChange} />
              <List
                select={hostSelected}
                location={location}
                fetchData={fetchDefenseHost}
                limit={HOST_LIMIT}
                height={height}
                renderRow={(h: any) => (
                  <Item
                    key={h.view_defense_host_information_id}
                    title={h.view_defense_host_information_name || EMPTY_TEXT}
                    mainDescription={returnIP(h.view_defense_host_information_DeviceIp, h.view_defense_host_information_DevicePort)}
                    secondaryDescription={`区域信息:${h.view_defense_host_information_areaLocationName || EMPTY_TEXT}`}
                    state={
                      <State>
                        <DefenseStatus status={h.view_defense_host_information_check_value} />
                      </State>
                    }
                    handle={<Icon type="right" size="sm" />}
                  />
                )}
              />
            </div>
          </Tabs>
        </div>
      </div>
    </>
  );
});

export default Home;
